<template>
  <Result type="success" :title="email" :desc="description">
    <template #actions>
      <Button size="large" type="primary">查看邮箱</Button>
      <Button size="large" style="margin-left: 8px" @click="goHomeHandle">返回首页</Button>
    </template>
  </Result>
</template>

<script>
  import { Result } from 'view-ui-plus'

  export default {
    name: 'RegisterResult',
    components: {
      Result
    },
    data() {
      return {
        description: '激活邮件已发送到你的邮箱中，邮件有效期为24小时。请及时登录邮箱，点击邮件中的链接激活帐户。',
        form: {}
      }
    },
    computed: {
      email() {
        const v = (this.form && this.form.email) || 'xxx'
        return `你的账户：${v} 注册成功`
      }
    },
    created() {
      this.form = this.$route.params
    },
    methods: {
      goHomeHandle() {
        this.$router.push({ name: 'login' })
      }
    }
  }
</script>

<style scoped></style>
