<template>
  <div>
    <Form ref="ruleForm" :model="form" :rules="rule">
      <FormItem prop="mobile">
        <Input prefix="ios-phone-portrait" v-model="form.mobile" size="large" placeholder="请输入手机号码" />
      </FormItem>

      <Row :gutter="16">
        <Col :span="16">
          <FormItem prop="captcha">
            <Input prefix="ios-mail-outline" v-model="form.captcha" size="large" placeholder="请输入验证码" />
          </FormItem>
        </Col>
        <Col :span="8">
          <Button
            size="large"
            :disabled="state.smsSendBtn"
            @click.stop.prevent="getCaptcha"
            v-text="(!state.smsSendBtn && '获取验证码') || state.time + ' s'"
          />
        </Col>
      </Row>
    </Form>
  </div>
</template>

<script>
  import { mapActions } from 'vuex'
  import { getSmsCaptcha } from '@/api/login'

  export default {
    name: 'LoginPhone',
    emits: ['valid-fail', 'fail', 'success'],
    data() {
      return {
        form: {
          mobile: '',
          captcha: ''
        },
        rule: {
          mobile: [{ required: true, validator: this.validateMobile, trigger: 'change' }],
          captcha: [{ required: true, message: '请输入验证码', trigger: 'blur' }]
        },
        state: {
          time: 60,
          smsSendBtn: false
        }
      }
    },
    methods: {
      ...mapActions(['login']),
      validateMobile(rule, value, callback) {
        if (!value) {
          callback(new Error('请输入手机号码'))
        }
        if (!new RegExp(/^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/).test(value)) {
          callback(new Error('手机号码格式不正确'))
        } else {
          callback()
        }
      },
      getCaptcha(e) {
        e.preventDefault()
        const { state, form } = this
        this.$refs.ruleForm.validateField('mobile', error => {
          if (!error) {
            state.smsSendBtn = true
            const interval = window.setInterval(() => {
              if (state.time-- <= 0) {
                state.time = 60
                state.smsSendBtn = false
                window.clearInterval(interval)
              }
            }, 1000)
            const hide = this.$Message.loading('验证码发送中..', 0)
            getSmsCaptcha({ mobile: form.mobile })
              .then(res => {
                setTimeout(hide, 0)
                this.$Notice.success({
                  title: '系统提示',
                  desc: `验证码获取成功，您的验证码为：${res.captcha}`,
                  duration: 8
                })
              })
              .catch(err => {
                setTimeout(hide, 1)
                clearInterval(interval)
                state.time = 60
                state.smsSendBtn = false
                this.requestFailed(err)
              })
          } else {
            this.$Message.error(error)
          }
        })
      },
      handleLogin() {
        this.$refs.ruleForm.validate(valid => {
          if (valid) {
            const loginParams = { ...this.form }
            this.login(loginParams)
              .then(res => this.$emit('success', res))
              .catch(err => this.$emit('fail', err))
          } else {
            this.$emit('valid-fail')
            const refThat = this.$refs.ruleForm
            const fields = Object.keys(refThat.model)
            const errors = []
            fields.forEach(field =>
              refThat.validateField(field, error => {
                if (error) errors.push(error)
              })
            )
            this.$Message.error(errors.join('；'))
          }
        })
      }
    }
  }
</script>
