<template>
  <div>
    <Form ref="ruleForm" :model="form" :rules="rule">
      <FormItem prop="username">
        <Input prefix="ios-contact-outline" v-model="form.username" size="large" placeholder="请输入帐号 / admin" />
      </FormItem>
      <FormItem prop="password">
        <Input type="password" prefix="ios-lock-outline" v-model="form.password" size="large" autocomplete="false" placeholder="请输入密码 / admin" />
      </FormItem>
      <Row :gutter="0" v-if="isPictureCode">
        <Col :span="16">
          <FormItem prop="captcha">
            <Input prefix="ios-mail-outline" v-model="form.captcha" size="large" placeholder="请输入验证码" />
          </FormItem>
        </Col>
        <Col :span="8" class="captcha">
          <img v-if="requestCodeSuccess" :src="randCodeImage" @click="handleChangeCheckCode" />
          <img v-else src="~@/assets/login/checkCode.png" @click="handleChangeCheckCode" />
        </Col>
      </Row>
    </Form>
    <Verify
      v-if="!isPictureCode"
      ref="verify"
      :mode="'pop'"
      :imgSize="{ width: '400px', height: '200px' }"
      :captchaType="captchaType"
      @on-success="verifySuccess"
      @on-error="verifyError"
    />
  </div>
</template>

<script>
  import md5 from 'md5'
  import { mapActions } from 'vuex'
  import { Verify } from '@/components'
  import { getAction } from '@/api/manage'
  import { CAPTCHA_TYPE } from '@/utils/constants'

  export default {
    name: 'LoginAccount',
    components: { Verify },
    emits: ['valid-fail', 'fail', 'success'],
    data() {
      return {
        loginType: 0,
        verifyForce: false,
        requestCodeSuccess: true,
        randCodeImage: '',
        form: {
          username: 'admin',
          password: 'admin',
          captcha: 'admin'
        },
        rule: {
          username: [
            {
              required: true,
              validator: this.handleUsernameOrEmail,
              message: '请输入帐号'
            }
          ],
          password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
          captcha: [{ required: true, message: '请输入验证码', trigger: 'blur' }]
        }
      }
    },
    computed: {
      captchaType() {
        const min = 0
        const max = 100
        const random = Math.floor(Math.random() * (max - min)) + min
        let captchaTypeValue = CAPTCHA_TYPE.clickWord
        if (random % 3 === 0) {
          captchaTypeValue = CAPTCHA_TYPE.blockPuzzle
        } else if (random % 3 === 1) {
          captchaTypeValue = CAPTCHA_TYPE.pictureCode
        }
        return this.$config.captchaType || captchaTypeValue
      },
      isPictureCode() {
        return this.verifyForce || this.captchaType === CAPTCHA_TYPE.pictureCode
      }
    },
    created() {
      this.handleChangeCheckCode()
    },
    methods: {
      ...mapActions(['login']),
      handleChangeCheckCode() {
        this.currdatetime = new Date().getTime()
        const URL = `https://api.boot.jeecg.com/sys/randomImage/${this.currdatetime}`
        getAction(URL)
          .then(res => {
            if (res.success) {
              this.randCodeImage = res.result
              this.requestCodeSuccess = true
            } else {
              this.$Message.error(res.message)
              this.requestCodeSuccess = false
            }
          })
          .catch(() => {
            this.requestCodeSuccess = false
          })
      },
      handleUsernameOrEmail(rule, value, callback) {
        const regex = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((\.[a-zA-Z0-9_-]{2,3}){1,2})$/
        if (regex.test(value)) {
          this.loginType = 0
        } else {
          this.loginType = 1
        }
        callback()
      },
      handleLogin() {
        this.$refs.ruleForm.validate(valid => {
          if (valid) {
            if (this.isPictureCode) {
              this.handleSubmit()
            } else {
              this.$refs.verify.show()
            }
          } else {
            this.$emit('valid-fail')
            this.alertMsg()
          }
        })
      },
      alertMsg() {
        const refThat = this.$refs.ruleForm
        const fields = Object.keys(refThat.model)
        const errors = []
        fields.forEach(field =>
          refThat.validateField(field, error => {
            if (error) errors.push(error)
          })
        )
        this.$Message.error(errors.join('；'))
      },
      handleSubmit() {
        const loginParams = { ...this.form }
        loginParams.password = md5(this.form.password)
        this.login(loginParams)
          .then(res => this.$emit('success', res))
          .catch(err => this.$emit('fail', err))
      },
      verifySuccess(params) {
        this.form.code = params.captchaVerification
        this.handleSubmit()
      },
      verifyError() {
        this.verifyForce = true
        this.$Message.error('验证码组件异常，强制启用图形验证码')
      }
    }
  }
</script>
