<template>
  <div :class="`${prefixCls}-container`">
    <div :class="`${prefixCls}-container-form`">
      <Form class="login-layout" @keyup.enter="handleSubmit">
        <Tabs :modelValue="customActiveKey" @on-click="handleTabClick">
          <TabPane name="tab1" label="账号密码登录">
            <LoginAccount
              v-show="customActiveKey === 'tab1'"
              ref="alogin"
              @valid-fail="validateFail"
              @success="requestSuccess"
              @fail="requestFailed"
            />
          </TabPane>
          <TabPane name="tab2" label="手机号登录">
            <LoginPhone v-show="customActiveKey === 'tab2'" ref="plogin" @valid-fail="validateFail" @success="requestSuccess" @fail="requestFailed" />
          </TabPane>
        </Tabs>
      </Form>
    </div>
    <div :class="`${prefixCls}-container-remember`">
      <Checkbox v-model="rememberMe">记住密码</Checkbox>
      <RouterLink :to="{}">忘记密码</RouterLink>
    </div>
    <div :class="`${prefixCls}-container-submit`">
      <Button size="large" type="primary" @click.stop.prevent="handleSubmit">登录</Button>
    </div>
    <div :class="`${prefixCls}-container-other`">
      <span>其他登录方式</span>
      <img src="~@/assets/login/wx.svg" alt="微信" />
      <img src="~@/assets/login/qq.svg" alt="QQ" />
      <img src="~@/assets/login/wb.svg" alt="微博" />
      <RouterLink :to="{ name: 'register' }" :class="`${prefixCls}-container-other-register`">注册账户</RouterLink>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import { timeFix } from '@/utils/util'
  import LoginPhone from './components/LoginPhone'
  import LoginAccount from './components/LoginAccount'

  export default {
    components: {
      LoginPhone,
      LoginAccount
    },
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}login`,
        customActiveKey: 'tab1',
        rememberMe: false
      }
    },
    computed: {
      ...mapState({
        homeRouteName: state => state.app.homeRouteName,
        userInfo: state => state.user.userInfo
      })
    },
    methods: {
      verifySuccess(params) {
        this.$refs.alogin.verifySuccess(params)
      },
      handleTabClick(key) {
        this.customActiveKey = key
      },
      handleSubmit() {
        if (this.customActiveKey === 'tab1') {
          this.$refs.alogin.handleLogin()
        } else {
          this.$refs.plogin.handleLogin()
        }
      },
      requestSuccess(res) {
        this.$router.push({ name: this.homeRouteName })
        setTimeout(() => this.$Message.success(`${timeFix()}，${this.userInfo.name}`), 1000)
      },
      requestFailed(err) {
        this.validateFail()
        this.$Notice.error({
          title: '系统提示',
          desc: err.toString()
        })
      },
      validateFail() {}
    }
  }
</script>
