<template>
  <div :class="`${prefixCls}-container`">
    <Form ref="form" :model="form" :rules="rule">
      <FormItem prop="username">
        <Input prefix="ios-contact-outline" v-model="form.username" size="large" placeholder="请输入账户名" />
      </FormItem>

      <Poptip word-wrap trigger="focus" placement="right-start" v-model="state.passwordLevelChecked">
        <template #content>
          <div :style="{ width: '240px' }">
            <div :class="passwordLevelClass">
              强度：<span>{{ passwordLevelName }}</span>
            </div>
            <Progress :percent="state.percent" :hide-info="true" :stroke-color="passwordLevelColor" />
            <div style="margin-top: 10px">
              <span>请至少输入 6 个字符。请不要使用容易被猜到的密码。</span>
            </div>
          </div>
        </template>
        <FormItem prop="password">
          <Input
            type="password"
            prefix="ios-lock-outline"
            v-model="form.password"
            size="large"
            @click="handlePasswordInputClick"
            autocomplete="false"
            placeholder="至少6位密码，区分大小写"
          />
        </FormItem>
      </Poptip>

      <FormItem prop="password2">
        <Input type="password" prefix="ios-lock-outline" v-model="form.password2" size="large" autocomplete="false" placeholder="确认密码" />
      </FormItem>
      <FormItem prop="mobile">
        <Input prefix="ios-phone-portrait" v-model="form.mobile" size="large" placeholder="请输入手机号码" />
      </FormItem>
      <Row :gutter="16">
        <Col :span="16">
          <FormItem prop="captcha">
            <Input prefix="ios-mail-outline" v-model="form.captcha" size="large" placeholder="请输入验证码" />
          </FormItem>
        </Col>
        <Col :span="8">
          <Button
            size="large"
            :disabled="state.smsSendBtn"
            @click.stop.prevent="getCaptcha"
            v-text="(!state.smsSendBtn && '获取验证码') || state.time + ' s'"
          />
        </Col>
      </Row>
    </Form>

    <div :class="`${prefixCls}-container-submit`">
      <Button size="large" type="primary" :loading="registerBtn" @click.stop.prevent="handleSubmit" :disabled="registerBtn">注册</Button>
    </div>
    <div :class="`${prefixCls}-container-other-login`">
      <RouterLink :to="{ name: 'login' }">使用已有账户登录</RouterLink>
    </div>
  </div>
</template>

<script>
  import { mixinDevice } from '@/utils/mixin'
  import { getSmsCaptcha } from '@/api/login'

  const levelNames = {
    0: '低',
    1: '低',
    2: '中',
    3: '强'
  }
  const levelClass = {
    0: 'error',
    1: 'error',
    2: 'warning',
    3: 'success'
  }
  const levelColor = {
    0: '#ff0000',
    1: '#ff0000',
    2: '#ff7e05',
    3: '#52c41a'
  }
  export default {
    name: 'Register',
    mixins: [mixinDevice],
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}login`,
        form: {
          username: '',
          password: '',
          password2: '',
          mobile: '',
          captcha: ''
        },
        rule: {
          username: [
            {
              required: true,
              validator: this.checkUsername,
              message: '请输入账户名'
            }
          ],
          password: [
            {
              required: true,
              validator: this.handlePasswordLevel,
              message: '至少6位密码，区分大小写'
            }
          ],
          password2: [
            {
              required: true,
              validator: this.handlePasswordCheck,
              message: '至少6位密码，区分大小写'
            }
          ],
          mobile: [
            {
              required: true,
              validator: this.handlePhoneCheck,
              message: '请输入手机号码',
              trigger: 'change'
            }
          ],
          captcha: [
            {
              required: true,
              validator: this.handleCaptchaCheck,
              message: '请输入验证码'
            }
          ]
        },
        state: {
          time: 60,
          smsSendBtn: false,
          passwordLevel: 0,
          passwordLevelChecked: false,
          percent: 10,
          progressColor: '#FF0000'
        },
        registerBtn: false
      }
    },
    computed: {
      passwordLevelClass() {
        return levelClass[this.state.passwordLevel]
      },
      passwordLevelName() {
        return levelNames[this.state.passwordLevel]
      },
      passwordLevelColor() {
        return levelColor[this.state.passwordLevel]
      }
    },
    methods: {
      checkUsername(rule, value, callback) {
        if (!value) {
          callback(new Error('请输入用户名'))
        } else {
          // 生产环境需要判断是否存在
          // const params = { username: value }
          // checkOnlyUser(params).then((res) => {
          //   if (res.success) {
          //     callback()
          //   } else {
          //     callback(new Error('用户名已存在'))
          //   }
          // })
          callback()
        }
      },
      handlePasswordLevel(rule, value, callback) {
        let level = 0

        // 判断这个字符串中有没有数字
        if (/[0-9]/.test(value)) {
          level++
        }
        // 判断字符串中有没有字母
        if (/[a-zA-Z]/.test(value)) {
          level++
        }
        // 判断字符串中有没有特殊符号
        if (/[^0-9a-zA-Z_]/.test(value)) {
          level++
        }
        this.state.passwordLevel = level
        this.state.percent = level * 30
        if (level >= 2) {
          if (level >= 3) {
            this.state.percent = 100
          }
          callback()
        } else {
          if (level === 0) {
            this.state.percent = 10
          }
          callback(new Error('密码强度不够'))
        }
      },

      handlePasswordCheck(rule, value, callback) {
        const password = this.form.password
        if (value === undefined) {
          callback(new Error('请输入密码'))
        }
        if (value && password && value.trim() !== password.trim()) {
          callback(new Error('两次密码不一致'))
        }
        callback()
      },

      handlePhoneCheck(rule, value, callback) {
        const reg = /^1[3456789]\d{9}$/
        if (!reg.test(value)) {
          callback(new Error('请输入正确手机号'))
        } else {
          // const params = { phone: value }
          // checkOnlyUser(params).then((res) => {
          //   if (res.success) {
          //     callback()
          //   } else {
          //     callback(new Error('手机号已存在'))
          //   }
          // })
          callback()
        }
      },

      handlePasswordInputClick() {
        if (!this.isMobile) {
          this.state.passwordLevelChecked = true
          return
        }
        this.state.passwordLevelChecked = false
      },
      handleCaptchaCheck(rule, value, callback) {
        if (!value) {
          callback(new Error('请输入验证码'))
        } else {
          callback()
        }
      },
      handleSubmit() {
        const { form, state, $router } = this
        this.$refs.form.validate(valid => {
          if (valid) {
            state.passwordLevelChecked = false
            $router.push({ name: 'registerResult', params: { ...form } })
          } else {
            const refThat = this.$refs.form
            const fields = Object.keys(refThat.model)
            const errors = []
            fields.forEach(field =>
              refThat.validateField(field, error => {
                if (error) errors.push(error)
              })
            )
            this.$Message.error(errors.join('；'))
          }
        })
      },
      getCaptcha(e) {
        e.preventDefault()
        const { form, state, $Message, $Notice } = this
        this.$refs.form.validateField('mobile', err => {
          if (!err) {
            state.smsSendBtn = true
            const interval = window.setInterval(() => {
              if (state.time-- <= 0) {
                state.time = 60
                state.smsSendBtn = false
                window.clearInterval(interval)
              }
            }, 1000)
            const hide = $Message.loading('验证码发送中..', 0)
            getSmsCaptcha({ mobile: form.mobile })
              .then(res => {
                setTimeout(hide, 0)
                $Notice.success({
                  title: '系统提示',
                  desc: `验证码获取成功，您的验证码为：${res.captcha}`,
                  duration: 8
                })
              })
              .catch(err => {
                setTimeout(hide, 1)
                clearInterval(interval)
                state.time = 60
                state.smsSendBtn = false
                this.requestFailed(err)
              })
          } else {
            this.$Message.error(err)
          }
        })
      },
      requestFailed(err) {
        this.$Notice.error({ title: '系统提示', desc: err.toString() })
        this.registerBtn = false
      }
    },
    watch: {
      'state.passwordLevel': function (val) {
        console.log(val)
      }
    }
  }
</script>
